form {
  padding: 2em 20px;
  background: lighten($black, 8%);
  margin: 0 auto;
  display: block;
  max-width: 520px;
  label {
    color: $grey;
  }
  input[type="text"],
  input[type="password"],
  input[type="email"],
  input[type="number"],
  input[type="date"],
  select {
    width: 100%;
    height: 40px;
    margin: 0.5rem 0 1em;
    border: none;
    padding: 1em;
    background: rgba($grey, 0.01);
    border: 1px solid rgba($grey, 0.1);
    color: $white;
    &:not([value=""]) {
      border-color: $primary;
    }
    &:focus {
      background: rgba($white, 0.2);
      &::placeholder {
        color: rgba($white, 0.1);
      }
    }
  }
  select {
    background: rgba($white, 0.2);
    color: rgba($black, 1);
    padding: 0 1em !important;
  }
  button {
    width: 100%;
  }
  small {
    display: block;
    color: rgba($white, 0.2);
    margin: 2em 0 0;
    text-align: center;
    a {
      color: $primary;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
